import React from 'react';
import ProjectsItems from './ProjectsItems';
import { useEffect } from 'react';
import useWindowDimensions from './getWindowsDimensions.js';
import { Colors } from './Colors';
// project img sources 
import projectImg1 from "../imgs/layer2.png";
import tunnelImg from "../imgs/ProjectImgs/tunnel.png";
import elecImg from "../imgs/ProjectImgs/electriccar.png";
import droneImg from "../imgs/ProjectImgs/drone.png";
import rfImg from "../imgs/ProjectImgs/rfsensor.png";
const ProjectsContainer = () => {
    const { height, width } = useWindowDimensions();
    const widthColPx = 1200;

    const projectsContainer = {
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        flexWrap: "wrap",
        height: "auto",

        width: "100%",
        maxWidth: "1600px",
        margin: "auto",
    };

    const projectsItemsSingleColbgc = {
        width: "90%",

        margin: "10px",
        borderRadius: "25px",

        height: "40vh",
    }
    const projectsItemsMulColbgc = {
        width: "45%",

        margin: "10px",
        borderRadius: "25px",

        height: "40vh",
        padding: "10px",
    }

    /* 
        if(category ==1) {
             
                <AlgoIcon style={{ width: "2rem", height:"2rem", marginRight: "15px", }} />
             Algorithm </div> ];
        }else if (category ==2){
           
             Firmware </div> ];
        }
        else if (category ==3){
           
             Coding </div> ];
        }
        else if (category =4){
           
             App Development </div> ];
        }
        else if (category ==5){
           
             Big data / Backend </div> ];
        }

    */
    return (
        <div style={projectsContainer}>
            <div style={width > widthColPx ? projectsItemsMulColbgc : projectsItemsSingleColbgc}>
                <ProjectsItems bgc={Colors.projectDark5} textCol={Colors.projectLight3} title=" " content="   AM, FM, DMB    AM, FM    . "
                    imgSrc={tunnelImg} category="2" 
                />
            </div>
            <div style={width > widthColPx ? projectsItemsMulColbgc : projectsItemsSingleColbgc}>
                <ProjectsItems bgc={Colors.projectLight3} textCol={Colors.projectDark5} title="  " content="      , CP(Charger Point)  "
                    imgSrc={elecImg} category="6" 
                />
            </div>
            <div style={width > widthColPx ? projectsItemsMulColbgc : projectsItemsSingleColbgc}>
                <ProjectsItems bgc={Colors.projectDark5} textCol={Colors.mainColor} title=" () RF  " content="  RF      (CPU) "
                    imgSrc={droneImg} category="6"
                />
            </div>
            <div style={width > widthColPx ? projectsItemsMulColbgc : projectsItemsSingleColbgc}>
                <ProjectsItems bgc={Colors.projectLight3} textCol={Colors.projectDark5} title="RF Plasma Chamber    " content="   .       (NuttyData)"
                    imgSrc={rfImg} category="6"
                />
            </div>
             

        </div>
    )
}

export default ProjectsContainer